body {
    margin:0;
}

.main {
    background: #FFFFFF;
    font-family:'Roboto'; 
    display: flex;
    align-items: center;
    justify-content: center;
}

.info {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1440px;
    margin-top: 96px;
    margin-bottom: 96px;
}

.picture img{
    width: 505px;
    height: 330px;
    margin-right: 95px;
}

.content {
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 1000px) {
    .main {
        .info{
            flex-direction: column;
            width: 100%;
            margin-top: 56px;
            margin-bottom: 0;
        }
    
        .picture{
            max-width: 505px;
        
            img {
                margin-right: 0;
                width: 100%;
                height: auto;
            }
        }
    
        .content{
            margin-top: 16px;
            max-width: 380px;
            margin-left: 16px;
            margin-right: 16px;

            p{
                width: auto;
            }
        }

    }

}

@media screen and (max-width: 500px) {
    .main {
        .info{
            margin-top: 0;
        }
    }
}
    

.content h2 {
    font-weight: 700;
    font-size: 42px;
    line-height: 120%;
    color: #121212;
    margin: 0;
}
.content p {
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    width: 380px;
    color: #121212;
    margin-bottom: 48px;
    margin-top: 24px;
}

.content button {
    background: #E4C1AE;
    border-radius: 32px;
    width: 128px;
    height: 48px;
    color: #FFFFFF;
    border: 0;
    font-weight: 700;
}


